.SmallCompanyBlock {
  transition: 0.2s ease all;
  height: 267px;
  max-width: 240px;
  margin-bottom: 50px;
  box-shadow: 4px 4px 4px #e5e5e5;
  border-radius: 10px;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  display: flex;
  margin-right: 5px;
  margin-left: 5px;
  animation: opacityAnimation 1s ease;
}
.companyImg {
  transition: 0.2s ease all;
  width: 100%;
  height: 150px;
  border-radius: 10px;
}
.description {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 9px;
}
.companyNameWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.companyName {
  font-weight: bold;
  font-size: 18px;
  align-items: center;
}
.companyType {
  font-weight: bold;
  font-size: 13px;
  color: #e32a6c;
}
.companyTitle {
  font-size: 14px;
  color: #4f4f4f;
}
.topBlockText {
  flex: 1;
}
.bottomBlockText {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 25px;
  line-height: 25px;
}
.leftText {
  font-weight: bold;
  font-size: 12px;
}
.rightText {
  font-weight: 500;
  font-size: 12px;
}
.circle {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: #e32a6c;
  margin-left: 10px;
  margin-right: 5px;
  align-self: center;
}
.row {
  display: flex;
  flex-direction: row;
}
.eye {
  height: 16px;
  width: 16px;
  margin-right: 5px;
  align-self: center;
}
@keyframes opacityAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 1060px) {
  .SmallCompanyBlock {
    height: 350px;
    max-width: calc(33.3% - 10px);
  }
  .companyImg {
    height: 200px;
  }
}
@media screen and (max-width: 960px) {
  .SmallCompanyBlock {
    height: 320px;
    max-width: calc(33.3% - 10px);
  }
  .companyImg {
    height: 180px;
  }
}
@media screen and (max-width: 860px) {
  .SmallCompanyBlock {
    height: 290px;
    max-width: calc(33.3% - 10px);
  }
  .companyImg {
    height: 160px;
  }
}
@media screen and (max-width: 760px) {
  .SmallCompanyBlock {
    height: 350px;
    max-width: calc(50% - 10px);
  }
  .companyImg {
    height: 190px;
  }
}
@media screen and (max-width: 560px) {
  .SmallCompanyBlock {
    height: 320px;
    max-width: calc(50% - 10px);
  }
  .companyImg {
    height: 165px;
  }
}
