.Company {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
  transition: 0.3s ease all;
}

.videoBlock {
  flex: 5;
  padding-right: 20px;
}
.video {
  margin-bottom: 10px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.videoDescription {
  margin-top: 40px;
  font-size: 16px;
  line-height: 24px;
  color: #4f4f4f;
}
.videoDescription span {
  font-weight: bold;
}
.desc {
  flex: 5;
}
.desc h3 {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
}
.desc .typeOfPati {
  font-size: 18px;
  line-height: 21px;
}
.desc .dayOfWeek {
  width: 54px;
  height: 22px;
  background: #36cc33;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0 40px 0;
}
.desc .timeBlock {
  display: flex;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 10px;
}
.desc .timeBlock img {
  margin-right: 16px;
}
.desc .rightTimeBlock {
  display: flex;
  width: 100%;
}
.desc .rightTimeBlock span:first-child {
  flex: 1;
}
.desc .rightTimeBlock span:last-child {
  margin-left: 10px;
  flex: 3;
}
.type {
  color: #e32a6c;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  padding-left: 20px;
}
.flex {
  display: flex;
}
.headerCompany {
  height: 100px;
  display: flex;
  align-items: center;
}
.toHome {
  display: inline-block;
  text-decoration: none;
  color: #000;
  font-size: 18px;
}
.smallMap {
  cursor: pointer;
  background-color: pink;
  width: 100%;
  height: 85px;
  border: 1px solid #4f4f4f;
  margin-top: 40px;
}
@media screen and (max-width: 760px) {
  .headerCompany {
    height: 50px;
    transition: 0.3s ease all;
  }
  .toHome {
    display: none;
  }
  .flex {
    flex-direction: column;
  }
  .video {
    width: 100%;
    height: 400px;
    border-radius: 0;
  }
  .Company {
    transition: 0.3s ease all;
    padding: 0;
  }
  .videoBlock {
    padding-right: 0px;
  }
  .row {
    padding-left: 10px;
  }
  .videoDescription {
    padding: 10px;
    margin-top: 0;
  }
  .desc {
    padding: 0 10px;
  }
  .desc .dayOfWeek {
    margin: 10px 0 20px 0;
  }
}
@keyframes toLeft {
  0% {
    right: 0px;
  }
  100% {
    right: 200px;
  }
}
@keyframes toRight {
  0% {
    right: 200px;
  }
  100% {
    right: 0px;
  }
}
