.SlideSideMenuWrap {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
}
.SlideSideMenu {
  box-shadow: 0 0 50px 0px #999;
  position: absolute;
  top: 0;
  width: 200px;
  height: 100%;
  padding: 10px;
  background-color: #eef1f6;
  overflow: hidden;
  transition: 0.5s ease-out;
  padding-top: 50px;
}
.sideMenuUl {
  line-height: 30px;
  padding-left: 20px;
  list-style-type: none;
}
@keyframes on {
  0% {
    right: -300px;
  }
  100% {
    right: 0;
  }
}
@keyframes off {
  0% {
    right: 0px;
  }
  100% {
    right: -300px;
  }
}
