.headerContainer {
  width: 100%;
  height: 100px;
  background-color: #fff;
}
.header {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1200px;
  height: 100px;
  display: flex;
  align-items: center;
  background-color: #e5e5e5;
}
.party_live {
  display: inline-block;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  font-size: 24px;
  letter-spacing: 0.05em;
  transition: 0.3s ease all;
  color: #323232;
}
.live {
  display: inline-block;
  background-color: #e32a6c;
  color: #fff;
  border-radius: 5px;
  margin-left: 3px;
  padding: 0 7px;
}
.homeContentWrap {
  padding-top: 100px;
  max-width: 1040px;
  margin: 0 auto;
  transition: 0.3s ease all;
}
.navContainer {
  margin: 0 25px;
  display: flex;
  justify-content: space-between;
}
.homeContent {
  margin-top: 50px;
  margin-right: 20px;
  margin-left: 20px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.burger {
  display: none;
}
@media screen and (max-width: 760px) {
  body {
    background-color: #eef1f6;
  }
  .header {
    justify-content: center;
    height: 48px;
    padding: 0 5px;
    transition: 0.3s ease all;
  }
  .headerContainer {
    height: 48px;
    background-color: #e5e5e5;
  }
  .burger {
    display: inline-block;
    position: absolute;
    right: 20px;
    top: 17px;
    width: 17px;
    height: 14px;
    cursor: pointer;
    z-index: 1;
  }
  .burger > span,
  .burger > span::before,
  .burger > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 1.5px;
    background-color: #616161;
  }
  .burger > span::before {
    content: "";
    top: 12px;
  }
  .burger > span::after {
    content: "";
    top: 6px;
  }
  .homeContentWrap {
    padding-top: 10px;
    transition: 0.3s ease all;
  }
  .homeContent {
    transition: 0.3s ease all 0.2s;
    margin-top: 10px;
    margin-right: 5px;
    margin-left: 5px;
  }
  .party_live {
    font-size: 20px;
  }
  .navContainer {
    justify-content: center;
  }
}

@keyframes toLeft {
  0% {
    right: 0px;
  }
  100% {
    right: 200px;
  }
}
@keyframes toRight {
  0% {
    right: 200px;
  }
  100% {
    right: 0px;
  }
}
