.сompanyNav {
  display: flex;
  height: 70px;
  /* border: 2px solid #c4c4c4; */
}
.companyNavBlock {
  display: flex;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border: 2px solid #c4c4c4;
  cursor: pointer;
  border-right: none;
}
.companyNavLink {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.companyNavBlock:last-of-type {
  border-right: 2px solid #c4c4c4;
}
.allText {
  font-weight: bold;
  font-size: 14;
}
.сompanyNavText {
  margin-top: 4px;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;
}
