.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup_inner {
  position: absolute;
  left: 18%;
  right: 18%;
  top: 15%;
  bottom: 15%;
  margin: auto;
  background: white;
}
.mapMarker {
  background-color: none;
  border: none;
}
.mapMarker img {
  width: 35px;
  height: 35px;
  border-radius: 3px;
}
.closeBtn {
  position: absolute;
  top: 10px;
  left: 10px;
  height: 25px;
  width: 25px;
  display: flex;
  font-size: 25px;
  justify-content: center;
  line-height: 21px;
  background-color: #fff;
  cursor: pointer;
  border-radius: 3px;
}
.closeBtn:hover {
  color: red;
}
